export const calculateReadibilityScore = text => {
  if (!text.length) {
    return 0
  }

  text = stripPostContent(text)

  const sentences = extractSentences(text)
  const words = extractWords(text)
  const avgSentenceLength = Math.floor(words.length / sentences.length)
  const totalSyllabsCount = words.reduce(
    (acc, val) => acc + countSyllabs(val),
    0
  )
  const avgSylabsNumberPerWord = Math.floor(totalSyllabsCount / words.length)

  let result = Math.floor(
    206.835 - 1.015 * avgSentenceLength - 84.6 * avgSylabsNumberPerWord
  )

  if (result < 0) {
    result = 0
  } else if (result > 100) {
    result = 100
  }

  return result
}

export const stripPostContent = text => {
  text = text.replace(/^#.*$/gim, "") // remove all headings (because they don't have a dot)
  text = text.replace(/^\|.*$/gim, "") // remove tables
  text = text.replace(/!?\[(.+)\]\(.*\)\.?/gi, "$1.") // extract names from images

  text = text.replace(/^\s*(\d+\.|[\-\+\*]).*$/gim, "") // remove numerated, list content
  text = text.replace(/<!--[\s\S]*-->/gim, "") // remove HTML comments
  text = text.replace(/^\s*\[.+\]:.+$/gim, "") // remove reference-style links
  text = text.replace(/\[(.+)\]\[.+\]/gi, "$1") // remove reference-style links
  text = text.replace(/{[\s\S]+?\}/gi, "") // remove snippets
  text = text.replace(/^```[\s\S]*?```/gim, "") // remove code blocks
  text = text.replace(/>.*$/gim, "") // remove quotes/citations
  text = text.replace(/\[(.+?)\](?:.*)(\.)?/gi, "$1$2") // replace link with link text
  text = text.replace(/`(.+?)`/gi, "X") // remove code in thicked text

  text = text.replace(/^\*+|[_=-]{2,}$/gim, "")
  text = text.replace(/^ {2,}.+$/gim, "")
  text = text.replace(/^\s*$(?:\r\n?|\n)/gm, "") // remove empty lines
  text = text.replace(/(?:\r\n?|\n)/gm, " ") // remove new lines
  text = text.replace(/ {2,}$/gi, " ") // replace multiple spaces with single one
  text = text.replace(/(\w)([.?!):])(\w)/gi, "$1$2 $3") // replace multiple spaces with single one
  return text
}

export const extractSentences = text => {
  if (!text.length) {
    return []
  }

  text = text.trim()

  if (text[text.length - 1] === ".") {
    text = text.substring(0, text.length - 1)
  }

  return text.split(". ")
}

export const countSyllabs = word => {
  if (!word.length) {
    return 0
  }

  word = word.trim()
  word = word.toLowerCase()
  word = word.replace(/(?:[^laeiouy]es|ed|[^laeiouy]e)$/, "")
  word = word.replace(/^y/, "")
  const result = word.match(/[aeiouy]{1,2}/g)
  return result ? result.length : 1
}

export const extractWords = text => {
  if (!text.length) {
    return []
  }

  text = text.replace(/(^\s*)|(\s*$)/gi, "")
  text = text.replace(/[ ]{2,}/gi, " ")
  text = text.replace(/\n /, "\n")
  return text.split(" ")
}

export const countWords = text => extractWords(text).length

export const validateReadability = text => {
  if (!text.length) {
    return {
      score: 0,
      tag: "empty",
      verdict: "Field is empty, please fill it",
    }
  }

  const score = calculateReadibilityScore(text)

  if (score >= 90 && score <= 100) {
    return {
      score,
      tag: "ok",
      verdict: "Super easy to read",
    }
  } else if (score >= 80 && score <= 89) {
    return {
      score,
      tag: "ok",
      verdict: "East to read",
    }
  } else if (score >= 70 && score <= 79) {
    return {
      score,
      tag: "ok",
      verdict: "Fairly easy to read",
    }
  } else if (score >= 60 && score <= 69) {
    return {
      score,
      tag: "ok",
      verdict: "Standard readability",
    }
  } else if (score >= 50 && score <= 59) {
    return {
      score,
      tag: "warn",
      verdict: "Fairly difficult to read",
    }
  } else if (score >= 30 && score <= 49) {
    return {
      score,
      tag: "error",
      verdict: "Difficult to read",
    }
  }

  return {
    score,
    tag: "error",
    verdict: "Very confusing to read",
  }
}

export const validateTextLength = (length, min, max = null) => {
  if (length === 0) {
    return {
      tag: "empty",
      verdict: "Field is empty, please fill it",
    }
  } else if (length < min) {
    return {
      tag: "error",
      verdict: "Too short",
    }
  } else if (max && length > max) {
    return {
      tag: "error",
      verdict: "Too long",
    }
  } else {
    return {
      tag: "ok",
      verdict: "Length is good",
    }
  }
}

export const validateKeywordInTitle = (keyword, title) => {
  const count = countKeywordInText(keyword, title)

  if (count === 0) {
    return {
      tag: "warn",
      verdict: `'${keyword}' is not present in the title`,
    }
  } else if (count === 1) {
    return {
      tag: "ok",
      verdict: `'${keyword}' is present in the title`,
    }
  }

  return {
    tag: "warn",
    verdict: `'${keyword}' occurs too many times it the title`,
  }
}

export const validateKeywordInDescription = (keyword, title) => {
  const count = countKeywordInText(keyword, title)

  if (count === 0) {
    return {
      tag: "warn",
      verdict: `'${keyword}' is not present in the description`,
    }
  } else if (count === 1 || count === 2) {
    return {
      tag: "ok",
      verdict: `'${keyword}' is present in the description`,
    }
  }

  return {
    tag: "error",
    verdict: `'${keyword}' occurs too many times it the description`,
  }
}

export const validateKeywordsInPostContent = (keywords, text) => {
  keywords = filterKeywords(keywords)
  text = stripPostContent(text)

  if (!keywords.length || !text.length) {
    return []
  }

  const result = []
  keywords.forEach(i => {
    result.push(validateKeywordInPostContent(i, text))
  })

  return result
}

