export const validateKeywordInPostContent = (keyword, text) => {
  text = text.replace(/^#.*$/gi, "")
  text = text.replace(/^```[\s\S]*?```/gim, "")
  const count = countKeywordInText(keyword, text)

  if (count === 0) {
    return {
      tag: "error",
      verdict: `'${keyword}' is not present in the post`,
    }
  }

  const wordsNumber = countWords(text)
  let ratio = Math.round(Number(count / wordsNumber).toFixed(2) * 100)

  if (ratio <= 1) {
    return {
      tag: "warn",
      verdict: `'${keyword}' saturation is low ${ratio}%`,
    }
  } else if (ratio <= 4.5) {
    return {
      tag: "ok",
      verdict: `'${keyword}' saturation is good ${ratio}%`,
    }
  }

  return {
    tag: "error",
    verdict: `'${keyword}' saturation is too high ${ratio}%`,
  }
}

/**
 * Every keyword should be present in at least one heading.
 */
export const validateKeywordInPostContentHeadings = (keyword, text) => {
  const headings = extractMarkdownHeadings(text)
  const regexp = RegExp(keyword, "gi")
  const count = headings.reduce(
    (acc, val) => acc + (val.match(regexp) || []).length,
    0
  )

  if (count === 0) {
    return {
      tag: "warn",
      verdict: `'${keyword}' not occur in any post headings`,
    }
  }

  return {
    tag: "ok",
    verdict: `'${keyword}' occur some post headings`,
  }
}

export const extractMarkdownHeadings = text => {
  const regexp = RegExp("#.*", "g")
  const result = []

  for (const match of text.matchAll(regexp)) {
    result.push(match[0])
  }

  return result
}

export const countKeywordInText = (keyword, text) => {
  const regexp = new RegExp(`( |^)${keyword}[. ]?`, "gi")
  return (text.match(regexp) || []).length
}

export const filterKeywords = keywords => {
  keywords = keywords
    .trim()
    .split(",")
    .reduce((acc, val) => {
      const v = val.trim().toLowerCase()
      if (v.length) {
        acc.push(v)
      }

      return acc
    }, [])

  return Array.from(new Set(keywords))
}

export const validateKeywords = (keywords, min) => {
  if (!keywords.length) {
    return {
      tag: "empty",
      verdict: "Field is empty, please fill it",
    }
  }

  keywords = filterKeywords(keywords)

  if (keywords.length < min) {
    return {
      tag: "warn",
      verdict: `You choose too few keywords ${keywords.length}. Try to generate about 5-15 (or more) unique keywords.`,
    }
  }

  return {
    tag: "ok",
    verdict: `You choose good number of keywords '${keywords.length}' keywords.`,
  }
}
