import React, {useEffect, useReducer} from "react"
import {graphql, useStaticQuery} from "gatsby"

import "./style.scss"
import KeywordPicker from "../KeywordPicker"
import TextAnalyzer from "../TextAnalyzer"
import Report from "../Report"
import {
  countWords,
  validateReadability,
  validateTextLength,
  validateKeywords,
  validateKeywordsInTitle,
  validateKeywordsInDescription,
  validateKeywordsInPostContent,
} from "../../utils/TextUtils"

let initialState = null

function createInitialState(params) {
  if (!initialState) {
    const sessionStorage =
      typeof window === "undefined"
        ? {getItem: () => ""}
        : window.sessionStorage
    const state = sessionStorage.getItem("analysers-state")

    if (state) {
      initialState = JSON.parse(state)
    } else {
      initialState = {
        Keywords: {
          content: "",
          notEmptyVerdict: validateKeywords("", params.keywordsMin),
        },
        Title: {
          content: "",
          length: 0,
          lengthVerdict: validateTextLength(
            0,
            params.titleMin,
            params.titleMax
          ),
          keywordsSaturation: validateKeywordsInTitle("", ""),
        },
        Description: {
          content: "",
          length: 0,
          lengthVerdict: validateTextLength(
            0,
            params.descriptionMin,
            params.descriptionMax
          ),
          keywordsSaturation: validateKeywordsInDescription("", ""),
          readibility: validateReadability(""),
        },
        "Post content": {
          content: "",
          length: 0,
          lengthVerdict: validateTextLength(0, params.postWordsMin),
          keywordsSaturation: validateKeywordsInPostContent("", ""),
          readibility: validateReadability(""),
        },
      }
    }
  }

  return initialState
}

function createReducer(params) {
  return (state, {type, payload}) => {
    switch (type) {
      case "updateKeywords":
        return {
          ...state,
          Keywords: {
            content: payload,
            notEmptyVerdict: validateKeywords(payload, params.keywordsMin),
          },
          Title: {
            ...state.Title,
            keywordsSaturation: validateKeywordsInTitle(
              payload,
              state.Title.content
            ),
          },
          Description: {
            ...state.Description,
            keywordsSaturation: validateKeywordsInDescription(
              payload,
              state.Description.content
            ),
          },
          "Post content": {
            ...state["Post content"],
            keywordsSaturation: validateKeywordsInPostContent(
              payload,
              state["Post content"].content
            ),
          },
        }
      case "updateTitle":
        return {
          ...state,
          Title: {
            content: payload,
            length: payload.length,
            lengthVerdict: validateTextLength(
              payload.length,
              params.titleMin,
              params.titleMax
            ),
            keywordsSaturation: validateKeywordsInTitle(
              state.Keywords.content,
              payload
            ),
          },
        }
      case "updateDescription":
        return {
          ...state,
          Description: {
            content: payload,
            length: payload.length,
            lengthVerdict: validateTextLength(
              payload.length,
              params.descriptionMin,
              params.descriptionMax
            ),
            keywordsSaturation: validateKeywordsInDescription(
              state.Keywords.content,
              payload
            ),
            readibility: validateReadability(payload),
          },
        }
      case "updateContent":
        return {
          ...state,
          "Post content": {
            content: payload,
            length: countWords(payload),
            lengthVerdict: validateTextLength(
              countWords(payload),
              params.postWordsMin
            ),
            keywordsSaturation: validateKeywordsInPostContent(
              state.Keywords.content,
              payload
            ),
            readibility: validateReadability(payload),
          },
        }
      default:
        throw new Error()
    }
  }
}

const PostMetaAnalyser = () => {
  const {
    site: {
      siteMetadata: {post},
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          post {
            descriptionMax
            titleMax
            titleMin
            descriptionMin
            postWordsMin
            keywordsMin
          }
        }
      }
    }
  `)

  const [state, dispatch] = useReducer(
    createReducer(post),
    createInitialState(post)
  )

  useEffect(() => {
    sessionStorage.setItem("analysers-state", JSON.stringify(state))
  })

  return (
    <section className="analysers">
      <div className="row">
        <div className="column column-50">
          <form>
            <KeywordPicker
              title="Keywords"
              instructions="The list of unique 5-15 keywords (or more) you want to target in your post. Separate keywords by the comma."
              name="Keywords"
              keywords={state.Keywords.content}
              placeholder="Enter in a keywords"
              onChange={value =>
                dispatch({type: "updateKeywords", payload: value})
              }
              className="box"
            />

            <TextAnalyzer
              title="Post Title"
              instructions="Copy & Paste your post title below or type it there."
              state={state.Title}
              max={post.titleMax}
              className="box">
              <textarea
                name="title"
                cols="30"
                rows="5"
                value={state.Title.content}
                placeholder="Enter in a post title"
                onChange={e =>
                  dispatch({type: "updateTitle", payload: e.target.value})
                }
              />
            </TextAnalyzer>

            <TextAnalyzer
              title="Post Description"
              instructions="Copy & Paste your post description below or type it there."
              state={state.Description}
              max={post.descriptionMax}
              checkReadibility={true}
              className="box">
              <textarea
                name="description"
                cols="30"
                rows="5"
                value={state.Description.content}
                placeholder="Enter in a post description"
                onChange={e =>
                  dispatch({
                    type: "updateDescription",
                    payload: e.target.value,
                  })
                }
              />
            </TextAnalyzer>

            <TextAnalyzer
              title="Post Content"
              instructions="Copy & Paste your post content below or type it there. Post analyser recognisez markdown semantic, so you can copy your post in markdown."
              state={state["Post content"]}
              max={post.postWordsMin}
              countWords={true}
              checkReadibility={true}
              className="box">
              <textarea
                name="content"
                cols="30"
                rows="10"
                value={state["Post content"].content}
                placeholder="Enter in a post content"
                onChange={e =>
                  dispatch({type: "updateContent", payload: e.target.value})
                }
              />
            </TextAnalyzer>
          </form>
        </div>
        <div className="column column-offset-5 column-45 ">
          <Report status={state} />
        </div>
      </div>
    </section>
  )
}

export default PostMetaAnalyser
