import React from "react"

import "./style.scss"

const KeywordPicker = ({
  title,
  instructions,
  onChange,
  name,
  placeholder,
  keywords = "",
  className = "",
}) => {
  return (
    <section className={`keyword-picker ${className}`}>
      <h2>{title}</h2>
      <p>{instructions}</p>
      <input
        className="keyword-picker"
        name={name}
        type="text"
        placeholder={placeholder}
        value={keywords}
        onChange={e => onChange(e.target.value)}
      />
    </section>
  )
}

export default KeywordPicker
