import React from "react"

import "./style.scss"

function calcProgressWidth(num, max) {
  const result = (num * 100) / max
  return result < 100 ? result : 100
}

const TextAnalyzer = ({
  children,
  title,
  instructions,
  state,
  max,
  className = "",
  checkReadibility = false,
  countWords = false,
}) => {
  const {length = 0, lengthVerdict, readibility} = state

  return (
    <section className={`text-analyzer ${className}`}>
      <h2>{title}</h2>
      <p>{instructions}</p>
      {children}

      <div className="progress">
        <div className={`progress-bar ${lengthVerdict.tag}`}>
          <span style={{width: `${calcProgressWidth(length, max)}%`}}>
            {length}
          </span>
        </div>
        <div className="progress-info">
          {countWords ? "Words" : "Characters"} number:{" "}
          <span className={`report-colored ${lengthVerdict.tag}`}>
            {length} / {max} - {lengthVerdict.verdict}
          </span>
        </div>
        {checkReadibility && (
          <div className="read-score">
            Readibility score:{" "}
            <span className={`report-colored ${readibility.tag}`}>
              {readibility.score} / 100 - {readibility.verdict}{" "}
              <span className="info">
                (i)
                <span className="tooltip">
                  The best text should contain short sentences and words.
                </span>
              </span>
            </span>
          </div>
        )}
      </div>
    </section>
  )
}

export default TextAnalyzer
