import React from "react"
import {graphql, useStaticQuery} from "gatsby"

import "./style.scss"
import adamfarynaImg from "../../images/adamfaryna-round.png"
import ExternalLink from "../ExternalLink"

const MadeBy = () => {
  const {
    site: {
      siteMetadata: {
        contact: {hash, name, url},
      },
    },
  } = useStaticQuery(graphql`
    query {
      site {
        siteMetadata {
          contact {
            hash
            name
            url
          }
        }
      }
    }
  `)

  return (
    <section className="made-by">
      <ExternalLink href={url} title={name}>
        <img alt={`Photo of ${name}`} src={adamfarynaImg} />
      </ExternalLink>
      <p>
        Made by{" "}
        <ExternalLink href={url} title={name}>
          {name}
        </ExternalLink>
      </p>
    </section>
  )
}

export default MadeBy
