import React, {Fragment} from "react"

import "./style.scss"
import {isNotEmpty} from "../../utils/Utils"

const Report = ({status}) => {
  return (
    <section className="report box">
      <h2>SEO Report</h2>
      <p>What&apos;s good and bad about your current SEO.</p>
      <ul>
        {Object.keys(status).map((key, idx) => (
          <Fragment key={`${idx}_${key}`}>
            {isNotEmpty(status[key].notEmptyVerdict) && (
              <li>
                <span
                  className={`report-colored ${status[key].notEmptyVerdict.tag}`}>
                  {key}: {status[key].notEmptyVerdict.verdict}
                </span>
              </li>
            )}
            {isNotEmpty(status[key].lengthVerdict) && (
              <li>
                <span
                  className={`report-colored ${status[key].lengthVerdict.tag}`}>
                  {key}: {status[key].lengthVerdict.verdict}
                </span>
              </li>
            )}
            {isNotEmpty(status[key].readibility) &&
              status[key].readibility.verdict !==
                status[key].lengthVerdict?.verdict && (
                <li>
                  <span
                    className={`report-colored ${status[key].readibility.tag}`}>
                    {key}: {status[key].readibility.verdict}
                  </span>
                </li>
              )}
            {status[key].keywordsSaturation &&
              status[key].keywordsSaturation.map((i, idx) => (
                <li key={`${idx}_${key}_${idx}`}>
                  <span className={`report-colored ${i.tag}`}>
                    {key} keyword: {i.verdict}
                  </span>
                </li>
              ))}
          </Fragment>
        ))}
      </ul>
    </section>
  )
}

export default Report
